/* 
=====================================================================
Hero
=====================================================================
*/
.producer-hero {
  /*! -- Stack Sliders -- */
}
.producer-hero .flickity-prev-next-button {
  transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
  -moz-transition: 0.3s ease;
  border-radius: 0;
  background: #252525;
  width: 2.78571429em;
  height: 5.57142857em;
}
.producer-hero .flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
  fill: #fff;
}
.producer-hero .flickity-prev-next-button:not(:hover) {
  opacity: 0.5;
}
.producer-hero .flickity-prev-next-button:hover {
  background: #252525;
}
.producer-hero .flickity-prev-next-button.previous {
  left: 0;
}
.producer-hero .flickity-prev-next-button.next {
  right: 0;
}
.producer-hero .flickity-prev-next-button:before {
  color: #fff;
}
.producer-hero .slider.slider--inline-arrows .flickity-prev-next-button {
  background: none;
}
.producer-hero .slider.slider--inline-arrows .flickity-prev-next-button:before {
  font-size: 1.78571429em;
  color: #252525;
  content: '\e802';
}
.producer-hero .slider.slider--inline-arrows .flickity-prev-next-button.previous:before {
  content: '\e801';
}
.producer-hero .slider.slider--columns .previous {
  left: 15px;
}
.producer-hero .slider.slider--columns .next {
  right: 15px;
}
.producer-hero .slider.border--round .slides li {
  border-radius: 6px;
  overflow: hidden;
}
.producer-hero .slider.boxed--border .slides li {
  border: 1px solid #ececec;
}
.producer-hero .slider .slides li .feature:not(.feature-9) {
  margin-bottom: 0;
}
.producer-hero .slide .boxed:last-child {
  margin-bottom: 0;
}
.producer-hero .slide:not([class*='col-']) > img:only-child {
  width: 100%;
}
.producer-hero .bg--dark .slider.slider--inline-arrows .flickity-prev-next-button:before,
.producer-hero .bg--primary .slider.slider--inline-arrows .flickity-prev-next-button:before,
.producer-hero .imagebg:not(.image--light) .slider.slider--inline-arrows .flickity-prev-next-button:before {
  color: #fff;
}
@media all and (max-width: 990px) {
  .producer-hero .flickity-prev-next-button {
    display: none;
  }
}
@media all and (min-width: 768px) {
  .producer-hero h2 + .slider,
  .producer-hero .h2 + .slider {
    margin-top: 3.71428571em;
  }
}
.producer-hero .slider.slider--ken-burns:not(.parallax) .slide.is-selected > .background-image-holder,
.producer-hero .slider.slider--ken-burns:not(.parallax) .slide.is-selected > img:only-child {
  animation: kenBurns 15s ease alternate infinite;
  -webkit-animation: kenBurns 15s ease alternate infinite;
}
.producer-hero .radial {
  margin-left: auto;
  margin-right: auto;
}
.producer-hero .radial .radial__label {
  transition: opacity 0.3s ease;
  -webkit-transition: opacity 0.3s ease;
}
.producer-hero .radial:not(.radial--active) .radial__label {
  opacity: 0;
}
.producer-hero .radial:not(:last-child) {
  margin-bottom: 1.85714286em;
}
@media all and (max-width: 767px) {
  .producer-hero .radial {
    margin-bottom: 1.85714286em;
  }
}
