  content: "";
  display: inline-block;
  flex-shrink: 0;
  height: 7px;
  left: 0;
  position: absolute;
  top: calc(7px - 4px);
  width: 8px;
}
.producer-member-list .accordion__content-inner ul li:last-of-type:before {
  background-color: transparent;
}
.producer-member-list .accordion__content {
  transition: height 0.3s ease-out;
  height: 0;
